.section-newsletter {
  background: $text-gray;
  padding-top: 4rem;
  padding-bottom: 4rem;
  position: relative;
  .title {
    font-size: 1.5rem;
    font-weight: 300;
    color: white;
  }
  form {
    padding-left: 2rem;
    padding-right: 2rem;
    .form-control {
      height: 2.5rem;
      &:focus {
        color: $text-gray;
        background-color: $white;
        border-color: $blue;
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba($color: $blue, $alpha: 0.25);
      }
    }
    .form-check-input{
      margin-bottom: 1rem;
      &:checked {
        background-color: $blue;
        border-color: $blue;
      }
      &:focus {
        border-color: $blue;
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba($color: $blue, $alpha: 0.25);
      }
    }
    .form-check-input[type=checkbox] {
      border-radius: 50%;
    }
    .form-check-label{
      a {
        color: $white;
        text-decoration: none;
        transition: all 0.3s;
        &:hover {
          color: $blue;
        }
      }
    }
    #btn-newsletter {
      font-weight: 400;
    }
  }
  .placaNewsletter {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $text-gray;
    text-align: center;
    p {
      font-size: 1.5rem;
      font-weight: bold;
      color: $white;
    }
  }
  .icon-whatsapp {
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(-50%) translateY(-50%);
    transition: all .3s;
    &:hover {
      transform: translateX(-50%) translateY(-50%) scale(1.1);
    }
    img {
      height: 5rem;
    }
  }
}