html, body {
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
  color: $text-gray;
  font-size: 10px;
  @media (min-width: 425px) {
    font-size: 12px;
  }
  @media (min-width: 768px) {
    font-size: 14px;
  }
  @media (min-width: 1200px) {
    font-size: 16px;
  }
}
.bold {
  font-weight: bold !important;
}
.content-title {
  display: flex;
  justify-content: center;
  @media (min-width: 992px) {
    justify-content: start;
  }
  img {
    width: 3rem;
    height: 3rem;
    margin-right: 0.5rem;
  }
}
.text-white {
  color: $white;
}
.text-green {
  color: $green;
}