#navbar {
  display: none;
  @media (min-width: 1200px) {
    display: block;
  }
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1030;
  transition: all 0.3s;
  .navbar {
    background-color: transparent;
    transition: all 0.3s;
    @media (max-width: 1199.98px) {
      max-width: 100%;
    }
    a {
      font-weight: 400;
      font-size: .8rem;
    }
    .navbar-brand {
      padding-top: 5px;
      padding-bottom: 5px;
      .logo-navbar {
        height: 80px;
      }
    }
    .navbar-nav {
      .nav-item {
        padding-left: 1.3rem;
        padding-right: 1.3rem;
        @media (min-width: 1400px) {
          padding-left: 2rem;
          padding-right: 2rem;
        }
        .nav-link {
          color: $white;
          font-weight: 600;
          border-bottom: 2px solid transparent;
          text-align: center;
          &.active:before {
            left: 0;
            right: 0;
            background: $green;
          }
        }
        &:hover {
          .nav-link:before {
            background: $green;
          }
        }
        .input-group {
          width: 12rem;
          .input-group-text {
            background: $white;
            border: none;
            border-radius: 0;
            border-top-left-radius: 50px;
            border-bottom-left-radius: 50px;
            padding: 0 0.5rem 0 1rem;
            img {
              width: 1rem;
            }
          }
          .form-control {
            background: $white;
            border: none;
            border-radius: 0;
            border-top-right-radius: 50px;
            border-bottom-right-radius: 50px;
            padding-left: 0.5rem;
            padding-right: 1rem;
            font-size: 0.8rem;
            &:focus {
              outline: none;
              box-shadow: none;
            }
          }
        }
      }
      .icon-perfil {
        position: relative;
        a {
          transition: all .3s;
          text-decoration: none;
          img {
            width: 2rem;
          }
        }
        .popup-login {
          position: absolute;
          width: 150px;
          top: 0;
          right: 0;
          padding-top: 10px;
          transform: translate(calc(50% - 1rem), 2rem);
          &:before {
            position: absolute;
            z-index: -1;
            content: "";
            left: calc(50% - 10px);
            top: 0px;
            border-style: solid;
            border-width: 0 10px 10px 10px;
            border-color: transparent transparent #ffffff transparent;
            transition-duration: .3s;
            transition-property: transform;
          }
          .d-grid {
            background-color: #ffffff;
            border-radius: 10px;
          }
        }
      }
      .icon-shopping-cart {
        position: relative;
        a {
          transition: all .3s;
          text-decoration: none;
          img {
            height: 2rem;
          }
        }
      }
    }
  }
}

#navbar-mobile {
  background-color: $blue;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  z-index: 1030;
  @media (min-width: 1200px) {
    display: none;
  }
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    & > div {
      width: 33.33%;
    }
    .img-logo {
      height: 50px;
    }
    .btn-menu {
      background: transparent;
      border: none;
      img {
        height: 20px;
      }
    }
    .btn-shopping-cart {
      padding-left: 0.5rem;
      margin-right: 1rem;
      img {
        height: 2rem;
      }
    }
  }
  .menu-open {
    position: fixed;
    top: 0;
    left: -100%;
    width: 70%;
    background-color: $blue;
    height: 100%;
    overflow-y: auto;
    padding: 1rem 1rem 3rem 1rem;
    z-index: 1030;
    transition: all .7s;
    .icon-close {
      text-align: end;
      img  {
        width: 15px;
      }
    }
    .contenedor-menu {
      margin-top: 5rem;
      .title-menu {
        font-size: 3rem;
        color: $white;
        font-weight: bold;
        text-align: center;
      }
      .list-menu {
        padding-left: 0;
        list-style: none;
        li {
          border-bottom: 1px solid #e2cdcd;
          padding: 1rem;
          &:nth-child(1) {
            border-top: none;
          }
          a {
            text-decoration: none;
            color: $white;
            font-weight: 500;
            font-size: 1.3rem;
            &:hover {
              color: $blue;
            }
            &:focus {
              color: $blue;
            }
          }
          .notificacion {
            display: inline-block;
            font-size: 1rem;
            font-weight: 700;
            line-height: 1;
            color: $white;
            text-align: center;
            white-space: nowrap;
            vertical-align: baseline;
            border-radius: 50%;
            background-color: $blue;
            padding: .6rem .8rem;
            margin-left: .8rem;
          }
        }
      }
      .redes-menu {
        ul {
          display: flex;
          justify-content: center;
          list-style: none;
          padding: 0;
          li {
            padding: 0 1rem;
            a {
              img {
                width: 3rem;
              }
            }
          }
        }
      }
    }
  }
}
    