footer {
  .section-footer1 {
    background-color: $white;
    padding-top: 3rem;
    padding-bottom: 3rem;
    .logo {
      height: 7rem;
    }
    .col-content {
      padding-top: 5rem;
    }
    h5 {
      color: $text-gray;
      font-weight: bold;
    }
    p {
      color: $text-gray;
      font-weight: 200;
    }
    ul {
      padding-left: 0;
    }
    li {
      list-style: none;
      margin-bottom: .3rem;
      .items-menu {
        color: $text-gray;
        font-weight: 200;
        text-decoration: none;
        &:hover {
          color: $blue;
        }
      }
    }
    small {
      color: $white;
    }
  }
  .section-mobil-footer {
    background-color: $white;
    color: $text-gray;
    padding: 2rem;
    .accordion-button {
      color: $text-gray;
      background-color: $white;
      font-weight: bold;
      font-size: 1.1rem;
      &:focus {
        border-color: $white;
        box-shadow: none;
      }
    }
    .accordion-body {
      background-color: $white;
      ul {
        list-style: none;
        color: $text-gray;
        padding-left: 0;
        a {
          text-decoration: none;
          color: $text-gray;
          &.hover {
            color: $blue;
          }
        }
      }
    }
    .logo {
      height: 7rem;
    }
    ul {
      list-style: none;
      color: $text-gray;
      padding-left: 0;
      a {
        text-decoration: none;
        color: $text-gray;
        &.hover {
          color: $blue;
        }
      }
    }
  }
  .dropdown-menu {
    min-width: 145px !important;
    padding: 0 !important;
    border-radius: 1.25rem !important;
    border: none;
    margin-bottom: -3px !important;

    li {
      margin-bottom: 0 !important;
    }
  }
  .dropdown-item {
    color: $text-gray;
    background-color: $white !important;
    &:hover {
      color: $white;
      background-color: $blue !important;
    }
  }
  .btn-idiomas {
    color: $white;
    border-radius: 1.2em;
    background-color: $text-gray;
    text-align: left;
    max-width: 145px;
    .arrow-down {
      width: 0.8rem;
    }
    &.show {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
    &:hover {
      color: $white;
    }
  }
  .section-footer2 {
    background: radial-gradient(circle, rgba(131, 106, 245, 1) 0%, rgba(131, 106, 245, 1) 10%, rgba(69, 36, 203, 1) 100%);
    padding: .5rem;
    p {
      color: $white;
      font-weight: 200;
    }
  }
}